/* Entire App */
html {
  background-color: #faf9f5;
  height: 100%;
  font-family: monospace;
  color: rgb(0, 0, 0);
}
strong {
  color: #de5f25;
}
a {
  color: #de5f25;
}

/* Blurb */
.blurb {
  text-align: center;
  font-size: 16;
}

/* Buttons */
.normal-button {
  background-color: transparent;
  color: #de5f25;
  font-style: oblique;
  border: none;
  font-size: 18px;
  font-family: monospace;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex: 1;
}
.hovered-button {
  background-color: #de602552;
  color: black;
  font-style: oblique;
  border: none;
  font-size: 18px;
  font-family: monospace;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex: 1;
}
.link-color {
  color: transparent;
}
.home-button {
  background-color: transparent;
  border: none;
  font-size: 40px;
  font-family: monospace;
  border-radius: 10px;
  border-style: groove;
  border-color: #258bde03;
  padding: 17px;
  display: flex;
  flex: 1;
}
.home-button strong {
  color: #629ccc;
}
.hamburger-button {
  background-color: transparent;
  border-color: #de5f25;
  width: 10%;
  height: 30px;
  display: flex;
  justify-content: center;
  border-radius: 5px;
}
.hamburger-button-open {
  background-color: rgb(201, 109, 109);
  border-color: #de5f25;
  width: 10%;
  height: 30px;
  display: flex;
  justify-content: center;
  border-radius: 5px;
}

/* Contact Me */
.contact-form {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: monospace;
}
.contact-input {
  background-color: transparent;
  border-radius: 5px;
  color: #de5f25;
  font-family: monospace;
  height: 20px;
  font-size: large;
  margin: 1%;
  width: 40%;
}
@media only screen and (max-width: 425px) {
  .contact-input {
    width: 90%;
  }
}
.contact-submit {
  font-size: medium;
  font-family: monospace;
  color: rgba(255, 245, 245, 0.927);
  background-color: #de5f25;
  margin-top: 1%;
  border-radius: 5px;
  height: 25px;
  width: fit-content;
  border-color: black;
}

/* Footer */
.footer-link {
  text-align: center;
  bottom: 5%;
  color: black;
  font: x-small;
  font-family: monospace;
  margin: 1%;
}

/* Header */
.header {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 425px) {
  .header {
    flex-direction: column;
  }
}

/* LinkBox */
.link-box {
  border-radius: 11px;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  margin-left: 1%;
  margin-right: 1%;
  text-align: center;
  width: 33px;
  height: 33px;
}
.link-box-container {
  display: flex;
  flex-direction: row;
  margin-top: 1%;
  justify-content: center;
}
.link-box-image {
  width: 27px;
  height: 27px;
  margin-top: 9%;
  margin-left: 12%;
}

/* News */
.article-text-color {
  color: #de5f25;
}
.news-container {
  border-style: groove;
  border-color: #de60251e;
  text-align: left;
  border-radius: 10px;
  margin: 0.3%;
}
.hovered-news-container {
  border-style: groove;
  background-color: #de602552;
  text-align: left;
  border-radius: 10px;
  margin: 0.3%;
}

/* Paragraphs */
.paragraph {
  margin: 5%;
  font-size: 120%;
}

/* Projects */
.project-icon {
  width: 17vw;
  height: 23vh;
  border: solid;
  z-index: -1;
}
@media only screen and (max-width: 767px) {
  .project-icon {
    width: 30vw;
    height: 25vh;
    border: transparent;
    z-index: -1;
  }
}
.project-tiles-container {
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
@media only screen and (max-width: 1037px) {
  .project-tiles-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 1%;
  }
}
.projects-more-back-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2%;
  margin-bottom: 3%;
}
.projects-more-back-button {
  background-color: transparent;
  border: none;
  color: #de5f25;
  font-family: monospace;
  font-size: 25px;
  font-style: oblique;
  height: 15%;
  width: fit-content;
  margin: 0%;
}
@media only screen and (max-width: 767px) {
  .projects-more-back-button {
    background-color: transparent;
    border: none;
    color: #de5f25;
    font-family: monospace;
    font-size: 40px;
    font-style: oblique;
    height: 15%;
    width: fit-content;
    margin: 0%;
  }
}

/* Terminal Text Box */
.paragraph-container {
  border-style: groove;
  border-color: #de60251e;
  font-size: medium;
  text-align: left;
  width: 50%;
  border-radius: 10px;
  margin: 0.5%;
}
@media only screen and (max-width: 800px) {
  .paragraph-container {
    font-size: small;
    width: 95%;
    margin: 0%;
  }
}
